<script>
    import Appbar from './components/appbar.svelte';
    import Socials from './components/socials.svelte';
    import{Rive, Layout} from 'rive-js';
    import {onMount} from "svelte";

    let canvas, rive;

    onMount(()=>{
        rive = new Rive({
            src: '../assets/avatar.riv',
            canvas: canvas,
            artboard: 'New Artboard',
            animations: 'black_tshirt',
            // layout: new Layout({fit: 'contain', alignment: 'topRight'}),
        });

        rive.play();
    });
</script>

<div id="homepage" class="w-screen h-screen bg-gray-200">
    <Appbar></Appbar>


    <canvas width="512" height="512" bind:this={canvas}></canvas>

    <Socials></Socials>
</div>