<script>
    import { scrollto } from "svelte-scrollto";
    let darkMode = false;

    function toggleDarkMode(){
        darkMode = !darkMode;
        console.log("toggled")
        if (darkMode) {
            document.documentElement.classList.add('mode-dark')
        } else {
            document.documentElement.classList.remove('mode-dark')
        }
    }



</script>

<div class="absolute right-0 py-10 px-20 space-x-10 ">
    <button on:click={toggleDarkMode}>toggle</button>
    <button use:scrollto={'#about'} class="text-blue-400 dark:text-white">About</button>
    <button use:scrollto={'#portfolio'} class="text-blue-400">Portfolio</button>
    <button use:scrollto={'#about'} class="text-blue-400 ">Contact</button>
</div>